<template>
        <div class="integral-tasks">
            <div class="rule-title">
                <div class="item item-one">
                    {{ $L('积分规则') }}
                </div>
                <div class="item item-two">
                    {{ $L('去赚积分') }}
                </div>
            </div>
            <div class="rule-list" v-if="sign.is_open === 1">
                <div class="rule-items">
                    <div class="left-item">
                        <img class="icon" src="../../../assets/img/distributor/rico_1.png">
                        <div class="bt">
                            <p class="big-tit">{{ $L('打卡签到') }}</p>
                            <p class="sm">{{ $L('每日打卡可获积分') }}</p>
                        </div>
                    </div>
                    <div class="right-item">
                        <el-button v-if="sign.is_gain === 1" :disabled="true">{{ $L('已签到') }}</el-button>
                        <el-button type="primary" v-if="sign.is_gain === 0" @click="doSign">{{ $L('去签到') }}</el-button>
                    </div>
                </div>
            </div>
            <div class="rule-list" v-if="vipcard.is_open === 1">
                <div class="rule-items">
                    <div class="left-item">
                        <img class="icon" src="../../../assets/img/distributor/rico_2.png">
                        <div class="bt">
                            <p class="big-tit">{{ $L('消费送积分') }}</p>
                            <p class="sm">{{ $L('每消费') }}{{ vipcard.money }}{{ $L('元得') }}{{ vipcard.integral }}{{ $L('积分') }}</p>
                        </div>
                    </div>
                    <div class="right-item">
                        <el-button type="primary" @click="goHome">{{ $L(' 去购物') }}</el-button>
                    </div>
                </div>
            </div>
            <div class="rule-list" v-if="register.is_open === 1">
                <div class="rule-items">
                    <div class="left-item">
                        <img class="icon" src="../../../assets/img/distributor/rico_3.png">
                        <div class="bt">
                            <p class="big-tit">{{ $L('会员注册') }}</p>
                            <p class="sm">{{ $L('说明：会员注册可获得') }}{{ register.integral }}{{ $L('积分') }}</p>
                        </div>
                    </div>
                    <div class="right-item">
                        <el-button type="primary" v-if="register.is_gain === 0" :disabled="true">{{ $L('去注册') }}</el-button>
                        <el-button v-if="register.is_gain === 1" :disabled="true">{{ $L('已完成') }}</el-button>
                    </div>
                </div>
            </div>
            <div class="rule-list" v-if="wechat.is_open === 1">
                <div class="rule-items">
                    <div class="left-item">
                        <img class="icon" src="../../../assets/img/distributor/rico_4.png">
                        <div class="bt">
                            <p class="big-tit">{{ $L('关注公众号') }}</p>
                            <p class="sm">{{ $L('说明：关注微信公众号成功后自动获得') }}{{ wechat.integral }}{{ $L('积分') }}</p>
                        </div>
                    </div>
                    <div class="right-item">
                        <el-button type="primary" v-if="wechat.is_gain === 0">{{ $L('去关注') }}</el-button>
                        <el-button v-if="wechat.is_gain === 1" :disabled="true">{{ $L('已关注') }}</el-button>
                    </div>
                </div>
            </div>
            <div class="rule-list" v-if="distribute.is_open === 1">
                <div class="rule-items">
                    <div class="left-item">
                        <img class="icon" src="../../../assets/img/distributor/rico_4.png">
                        <div class="bt">
                            <p class="big-tit">{{ $L('发展下级分销商') }}</p>
                            <p class="sm">{{ $L('成功发展下级分销商得积分') }}{{ distribute.integral }}{{ $L('积分') }}</p>
                        </div>
                    </div>
                    <div class="right-item">
                        <el-button type="primary" v-if="distribute.is_gain === 0">{{ $L('去分享') }}</el-button>
                    </div>
                </div>
            </div>
            <div class="rule-list" v-if="share.is_open === 1">
                <div class="rule-items">
                    <div class="left-item">
                        <img class="icon" src="../../../assets/img/distributor/rico_5.png">
                        <div class="bt">
                            <p class="big-tit">{{ $L('分享链接') }}</p>
                            <p class="sm">{{ $L('分享商家或活动链接得积分') }}{{ share.integral }}{{ $L('积分') }}</p>
                        </div>
                    </div>
                    <div class="right-item">
                        <el-button type="primary" v-if="share.is_gain === 0">{{ $L('去分享') }}</el-button>
                        <el-button type="" :disabled="true" v-else>{{ $L('已分享') }}</el-button>
                    </div>
                </div>
            </div>
            <div class="no-data" v-if="sign.is_open === 0 && vipcard.is_open === 0 && register.is_open === 0 && wechat.is_open === 0 && distribute.is_open === 0 && share.is_open === 0">
                {{ $L('暂无数据') }}
            </div>
            <div class="error" v-if="errorShow">
                <el-alert
                        class="error-tips"
                        :title="errorTitle"
                        :type="errorType"
                        :description="errorTips"
                        :closable="false"
                        :center="true"
                        show-icon>
                </el-alert>
            </div>
        </div>

</template>

<script>

    import SectionTitle from "../../../components/common/sectionTitle";
    export default {
        name: "integralTasks",
        components: {SectionTitle},
        data (){
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                btnSize:"small",
                sign:{},
                share:{},
                distribute:{},
                register:{},
                vipcard:{},
                wechat:{},
                isSignLater:false,
            }
        },
        inject : ['reload'],
        created (){
            this.getData();
        },
        methods : {
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('gainIntegralPoint', {} , function (reData) {
                    if (reData.flag === 1) {
                        _self.sign = reData.data.sign;
                        _self.share = reData.data.share;
                        _self.distribute = reData.data.distribute;
                        _self.register = reData.data.register;
                        _self.vipcard = reData.data.vipcard;
                        _self.wechat = reData.data.wechat;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'integralShop'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'integralShop'});
                    });
                });
            },
            doSign() {
                const _self = this;
                _self.$api("doSign", {}, function (reData) {
                    if (reData && reData.flag === 1) {
                        _self.$message.success(_self.$L('签到成功！获得{$1}积分', [reData.data.score]));
                        // _self.getData();
                        _self.reload();
                        _self.isSignLater = true;
                    } else if (reData && reData.flag === 2) {
                        _self.$message.success(_self.$L('今天已签到！'));
                        _self.isSignLater = true;
                    } else {
                        _self.$message.error(_self.$L('签到失败！请稍后重试'));
                    }
                }, function (error) {
                    console.log(error);
                    _self.$message.error(_self.$L('签到失败！请稍后重试'));
                });
            },
            goHome(){
                this.$gotoWebHome();
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    .integral-tasks{
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        padding: 20px;
        .content{
            padding: 0 20px 20px;
            margin-top: 10px;
            background: #fff;
            .table{
                .info{
                    height: 50px;
                    .left{
                        width: 50px;
                        height: 50px;
                        float: left;
                    }
                    .msg{
                        float: left;
                        padding-left: 20px;
                        width: calc(100% - 60px);
                        .title{
                            color: #333;
                        }
                        .tips{
                            color: #999;
                            line-height: 25px;
                        }
                    }
                }
                .button{
                    button{
                        padding: 10px 30px;
                    }
                }
            }
        }
        .rule-title{
            padding: 20px;
            background: #fff;
            font-size: 14px;
            color: #333;
            margin-top: 10px;
            border-bottom: 1px solid #ebeef5;
            .item{
                display: inline-block;
                font-weight: 700;
                &.item-one{
                    width: 75%;
                }
                &.item-two{
                    text-align: center;
                    width: 25%;
                 }
            }
        }
        .rule-list{
            padding: 40px 20px;
            background: #fff;
            border-bottom: 1px solid #ebeef5;
            .rule-items{
                height: 60px;
                .left-item{
                    height: inherit;
                    display: inline-block;
                    width: 75%;
                    vertical-align: middle;
                    .bt{
                        padding-left: 20px;
                        float: left;
                        p{
                            line-height: 30px;
                            &.big-tit{
                                font-size: 16px;
                            }
                            &.sm{
                                color: #999;
                            }
                        }
                    }
                    .icon{
                        width: 60px;
                        height: inherit;
                        float: left;
                    }
                }
                .right-item{
                    height: inherit;
                    display: inline-block;
                    text-align: center;
                    width: 25%;
                    button{
                        width: 100px;
                    }
                }
            }
        }
        .no-data{
            background: #fff;
            text-align: center;
            color: #999;
            height: 100px;
            line-height: 100px;
            margin-bottom: 10px;
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>